import * as yup from 'yup';

export const loginSchema = yup.object().shape({
    username: yup
        .string()
        .required('Nazwa uytkownika jest wymagana!')
        .min(8, 'Nazwa uytkowniak musi mie 8 lub wicej znakw!'),
    password: yup
        .string()
        .required('Haso jest wymagane!')
        .min(8, 'Haso  musi mie 8 lub wicej znakw!'),
});
